<template>
    <div style="background:#F2F6FC;padding:10px">
        <el-row>
            <el-col :span="4">
                <el-select v-model="ip" placeholder="RedisIP" @change="changegeip" filterable >
                    <el-option v-for="s in hosts" :key="s" :value="s" :label="s"></el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row ref="form" :model="form">
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tooltip  placement="top">
                            <div slot="content">当前使用内存量 {{form.used_memory}}</div>
                            <el-button>USED MEMORY</el-button>
                        </el-tooltip>
                        <div @click="dialogTableVisible = true" style="weight:100%;padding:5px" >
                            <span   style="font-size:0.8cm;">
                                {{form.used_memory}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tooltip   placement="top">
                            <div slot="content">key的总数量  {{form.keys}}</div>
                            <el-button>TOTAL KEYS</el-button>
                        </el-tooltip>
                        <div @click="dialogTableVisible = true" style="weight:100%;padding:5px" >
                            <span   style="font-size:0.8cm;">
                                {{form.keys}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip  placement="top">
                            <div slot="content">已连接客户端的数量 {{form.connected_clients}}</div>
                            <el-button>CLIENTS</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:5px">
                            <span style="font-size:0.8cm;">
                                 {{form.connected_clients}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip   placement="top">
                            <div slot="content">具有过期时间的键的数量 {{form.expires}}</div>
                            <el-button>EXPIRES</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:5px">
                            <span style="font-size:0.8cm;">
                                 {{form.expires}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip  placement="top">
                            <div slot="content">﻿服务器每秒钟执行的命令数量 {{form.ops}}</div>
                            <el-button>OPS/SEC</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:5px">
                            <span style="font-size:0.8cm;">
                                 {{form.ops}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="4">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip   placement="top">
                            <div slot="content">﻿运行天数 {{form.uptime}}</div>
                            <el-button>UPTIME DAYS</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:5px">
                            <span style="font-size:0.8cm;">
                                 {{form.uptime}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#F2F6FC;">
                        <i-echarts style="width:100%;height: 400px;" :option="chartData"
                                   :loading="chartLoading"></i-echarts>
                    </el-card>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#F2F6FC;">
                        <i-echarts style="width:100%;height: 400px;" :option="opsChartData"
                                   :loading="chartLoading"></i-echarts>
                    </el-card>
                </div>
            </el-col>
        </el-row>
    </div>

</template>

<script>
  import service from './service'
  import IEcharts from 'vue-echarts-v3/src/lite.js'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/dataZoom'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/toolbox'
  import 'echarts/lib/component/title'

  export default {
    name: 'home',
    data () {
      return {
        currTab: '1',
        currRow: {},
        homeList: [],
        topList: [],
        hosts: [],
        ip: '',
        form: {
          used_memory: '',
          keys: '',
          connected_clients: '',
          expires: '',
          ops: '',
          uptime: ''
        },
        chartData: {
          title: {
            text: 'USED MEMORY(M/G)'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            type: 'line',
            areaStyle: {}
          }]
        },
        opsChartData: {
          title: {
            text: 'COMMANDS OPS(SEC)'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: 'line',
            areaStyle: {}
          }]
        },
        chartLoading: false
      }
    },
    methods: {
      getClusterInfo () {
        return service.getClusterInfo({ host: this.ip }).then((result) => {
          console.log(result.data)
          this.form.used_memory = result.data.used_memory
          this.form.keys = result.data.keys
          this.form.connected_clients = result.data.connected_clients
          this.form.expires = result.data.expires
          this.form.ops = result.data.ops
          this.form.uptime = result.data.uptime
        })
      },
      changegeip () {
        this.chartData.xAxis.data = []
        this.opsChartData.series = [{
          data: [],
          type: 'line',
          areaStyle: {}
        }]
        this.chartLoading = true
        this.getClusterInfo()
        this.getMemoryChartData()
        this.getOpsChartData()
        this.chartLoading = false
      },
      getMemoryChartData () {
        return service.getMemoryChartData({ host: this.ip }).then((res) => {
          console.log(res)
          this.chartData.xAxis.data = res.data.xaxis
          this.chartData.series = [{
            data: res.data.yaxis,
            type: 'line',
            areaStyle: {}
          }]
        })
      },
      getOpsChartData () {
        return service.getOpsChartData({ host: this.ip }).then((res) => {
          console.log(res)
          this.opsChartData.xAxis.data = res.data.xaxis
          this.opsChartData.series = [{
            data: res.data.yaxis,
            type: 'line',
            areaStyle: {}
          }]
        })
      },
      getHosts () {
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/redis/getHosts').then((res) => {
          console.log(res)
          this.hosts = res.data
        })
      },
      rowClick (row) {
        this.currRow = row
      }
    },
    computed: {},
    mounted () {
      this.chartLoading = true
      console.log('mounted', this.$t('home'))
      this.getClusterInfo()
      this.getHosts()
      this.getMemoryChartData()
      this.getOpsChartData()
      this.chartLoading = false
    },
    components: {
      IEcharts
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



