import axios from 'axios'

export default {
  getClusterInfo (param, url = '/redis/getClusterInfo') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params: param})
  },
  getMemoryChartData (param, url = '/redis/getMemoryChartData') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params: param})
  },
  getOpsChartData (param, url = '/redis/getOpsChartData') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params: param})
  }
}



